import Container from '@/components/Container'
import ShowContainer from '@/components/ShowContainer'

import Show from '@/pages/show'
import Bali from '@/pages/show/Bali'
import Rome from '@/pages/show/Rome'
import Maldives from '@/pages/show/Maldives'
import Saipan from '@/pages/show/Saipan'

const routes = [
	{
	  path: '/',
	  redirect: '/show'
	},
	{
	  path: '/',
	  name: '',
	  component: Container,
	  children: [
	  	{ path: 'show', name: '', component: Show,}
	  ]
	},
	{
		path: '/show',
		name: '',
		component: Container,
	  children: [
	  	{ path: 'Rome', name: '', component: Rome, },
	  	{ path: 'Bali', name: '', component: Bali, },
	  	{ path: 'Saipan', name: '', component: Saipan, },
	  	{ path: 'Maldives', name: '', component: Maldives, },
	  ]
	}
]

export default routes;


